@import url(https://fonts.googleapis.com/css2?family=Harmattan&display=swap);
body {
  background: linear-gradient(
    90deg,
    rgba(88, 0, 105, 0.9) 0%,
    rgba(88, 0, 105, 0.49) 100%
  );
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading-container {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: auto;
  width: 100%;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #09f;
  border-radius: 50%;
  height: 36px;
  width: 36px;

  -webkit-animation: spin 1s ease infinite;

          animation: spin 1s ease infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card-yugi-img:hover {
  box-shadow: 0 0 50px #000;
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.pagination-container ul li button {
  background-color: transparent;
  color: #fff;
}

.pagination-container ul li .Mui-selected {
  background-color: #fff;
  border: 3px solid #000;
  color: #000;
}

.home-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.home-container h1 {
  color: rgba(255, 255, 255, 1);
  font-size: 7.4rem;
  letter-spacing: 2.88rem;
  margin-bottom: 20rem;
  stroke: #000;
  text-align: end;
  text-shadow: 20px 20px 12px rgba(0, 0, 0, 0.2);
  -webkit-text-stroke: 3px #000;
}

.home-container img {
  width: 500px;
}

.search-container {
  position: relative;
}

.search-container-autocomplete {
  width: 300px;
}

.search-container-autocomplete label,
.search-container-autocomplete .MuiInputBase-input,
.search-container-autocomplete .MuiFormLabel-root.Mui-focused,
.search-container-autocomplete .MuiAutocomplete-endAdornment button {
  color: rgba(255, 255, 255, 0.7);
}

.search-container-autocomplete .MuiOutlinedInput-notchedOutline,
.search-container-autocomplete
  .MuiOutlinedInput-root.Mui-focused
  .MuiOutlinedInput-notchedOutline {
  border-color: rgba(255, 255, 255, 0.7);
}

.search-container-autocomplete
  .MuiOutlinedInput-root:hover
  .MuiOutlinedInput-notchedOutline {
  border-color: rgba(255, 255, 255, 0.7);
}

.search-container-collapse {
  bottom: 0;
  position: absolute;
  right: 0;
  width: 300px;
}

.search-container .MuiButton-containedPrimary {
  background-color: #003287;
  color: rgb(255, 255, 255);
}

.app-container {
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

.navbar-container {
  align-items: center;
  display: flex;
  height: 139px;
  justify-content: space-between;
  margin: 50px auto 30px;
  width: 1243px;
}

.navbar-container .nav-link img {
  height: 138px;
  width: 387px;
}

.navbar-container nav {
  width: 487px;
}

.navbar-container ul {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding: 0;
}

.navbar-container nav ul li .nav-link {
  color: rgba(255, 255, 255, 0.7);
  letter-spacing: 7.2px;
  font-family: 'Harmattan', sans-serif;
  font-size: 36px;
  text-decoration: none;
}

.navbar-container nav ul li:hover {
  -webkit-transform: scale(1.09);
          transform: scale(1.09);
}

.navbar-container nav ul li span:hover {
  color: #003287;
}

footer {
  bottom: 30px;
  display: flex;
  justify-content: center;
  position: relative;
}

footer p {
  color: rgba(255, 255, 255, 0.7);
}

footer p a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: underline;
}

@media only screen and (max-width: 1280px) {
  .navbar-container {
    flex-direction: column;
    margin: 50px auto 0;
    width: 100%;
  }

  .navbar-container nav {
    width: 80%;
  }

  .navbar-container .nav-link {
    text-align: center;
    width: 100%;
  }

  .navbar-container .nav-link img {
    height: 69px;
    width: 193.5px;
  }

  .navbar-container nav ul li .nav-link {
    font-size: 1.2rem;
    letter-spacing: 2px;
  }

  .home-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .home-container h1 {
    font-size: 2.2rem;
    letter-spacing: 1.5rem;
    margin: 0;
    stroke: #000;
    text-align: center;
    text-shadow: 0.7rem 0.7rem 0.5rem rgba(0, 0, 0, 0.2);
    -webkit-text-stroke: 1.5px #000;
  }

  .home-container img {
    justify-content: space-around;
    margin-bottom: 50px;
    width: 220px;
  }
}

